.header {
    &__logo {
        flex-shrink: 0;
        img {
            height: 150px;
            width: auto;
            flex-shrink: 0;
        }
    }
    &__inner {
        &-top {
            padding: 10px 0;
            border-bottom: 1px solid var(--iconColor);
        }
        &-bottom {
            box-shadow: var(--headerShadow);
            .dropdown-border {
                display: none;
            }
        }
    }
    &__nav {
        &-top {
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            flex-grow: 1;
            margin-left: 25px;
            font-weight: 600;
            a {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                i {
                    flex-shrink: 0;
                    margin-right: 10px;
                }
            }
        }
        &-bottom {
            font-weight: 600;
            li {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 25px;
            }
            .dropdown li {
                margin: 0;
            }
        }
    }
    &__btn {
        &-menu {
            display: none;
            align-items: center;
            justify-content: center;
        }
    }
}
ul.header__nav-bottom {
    padding: 20px 0;
}
@media(max-width: 991px) {
    .header {
        &__inner {
            &-bottom {
                position: relative;
                padding: 15px 0;
                .container > .flexRow {
                    align-items: center;
                    justify-content: space-between;
                }
                .dropdown-border {
                    display: flex;
                }
            }
        }
        &__btn {
            &-menu {
                display: flex;
            }
        }
        &__logo {
            img {
                height: 100px;
            }
        }
        &__nav {
            &-top {
                .dropdown {
                    display: none;
                }
                a {
                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }
            &-bottom {
                transform: translateX(-100%);
                position: absolute;
                left: 0;
                top: 100%;
                z-index: 2;
                background-color: var(--White);
                width: 100%;
                flex-direction: column;
                border-top: 1px solid var(--iconColor);
                box-sizing: border-box;
                transition: all .2s;
                &.hover {
                    transform: translateX(0);
                    transition: all .2s;
                }
                li, .dropdown {
                    flex-direction: column;
                    align-items: flex-start;
                    &:not(:last-child) {
                        margin: 0 0 10px 0;
                    }
                    .dropdown-list {
                        position: static;
                        padding: 10px 0;
                        margin-top: 10px;
                        border: none;
                        border-top: 1px solid var(--iconColor);
                        border-radius: 0;
                        width: 100%;
                        min-width: 100%;
                        box-sizing: border-box;
                        a {
                            padding: 0;
                            width: auto;
                        }
                    }
                }
            }
        }
    }
    ul.header__nav-bottom {
        padding: 15px;
    }
}
@media(max-width: 575px) {
    .header {
        &__logo {
            img {
                height: 80px;
            }
        }
        &__nav {
            &-top {
                flex-direction: row;
                align-items: center;
                justify-content: flex-end;
                a {
                    span {
                        display: none;
                    }
                    i {
                        margin: 0;
                        font-size: 20px;
                    }
                    &:first-child {
                        margin: 0 20px;
                    }
                }
            }
        }
    }
}